import { Pipe, PipeTransform } from '@angular/core';

import { Column } from './column';

@Pipe({
  name: 'orderBy',
  pure: false
})
export class OrderPipe implements PipeTransform {


  transform(value:Array<any>, prop?: string, direcion?,sortmode?:boolean): any { 
    if (!sortmode){
      return value;
    }
    if (!value) { 
      return [];  
    }  
    if ( !prop) {  
      return value  
    }  
    sortmode = false;
    if (value.length > 0) {  
     
      const _direction = direcion === false ? -1 : 1,      
        _isArr = Array.isArray(value),  
        _type = typeof value[0],  
        _flag = _isArr && _type === 'object' ? true : _isArr && _type !== 'object' ? false : true;  
     
      value.sort((a, b) => {  
        a = _flag ? a[prop] : a;  
        b = _flag ? b[prop] : b;  
     
        if (typeof a === 'string') {  
          return a > b ? -1 * _direction : 1 * _direction;  
        } else if (typeof a === 'number') {  
          return a - b > 0 ? -1 * _direction : 1 * _direction;  
        }  
      });  
      
    }  
    
    return value; 
    

  }

}
