import { Column } from './column';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class GridService {
  columns: Column[];
  data: any;
  addopt: boolean;
  delopt: boolean;
  public datas: any[] = [];
  constructor() { }
  OnLoadData(datas?: any[], col?: Column[],addopt?: boolean, delopt?: boolean) {
    this.columns = col;
    this.addopt = addopt;
    this.delopt = delopt;
    if (!datas || datas.length === 0){
      this.datas = new Array();
      this.datas = this.addRows(5);
    } else {
      this.datas = datas;
    }
  }
  addRows(lrow: Number): any[] {
    let vrbl = "";
    let vrg = "";
    let xdatas: any[] = [];
    for (let lx = 0; lx < lrow; lx++) {

      for (let i = 0; i < this.columns.length; i++) {
        vrbl += this.columns[i].code + ":" + '';
      }
      vrg = ',';
      if (lx == lrow) {
        vrg = '';
      }

      this.data = "{" + vrbl + "}";
      xdatas.push(this.data + vrg)
    }
    return xdatas;

  }
  GetColumns(): Column[] {
    return this.columns;
  }
  GetData(): any[] {
    return this.datas;
  }
  GetDelopt(): boolean {
    return this.delopt;
  }
  GetAddopt(): boolean {
    return this.addopt;
  }
  getActivedata():any{
    return this.data;
}

}
